import React from 'react';
import { render, screen, fireEvent } from '@testing-library/react';
import WelcomeScreen from '../../components/WelcomeScreen';

describe('WelcomeScreen', () => {
  const mockOnStartGame = jest.fn();
  const mockOnPlayWelcomeMusic = jest.fn();

  beforeEach(() => {
    jest.clearAllMocks();
  });

  it('should render welcome screen correctly', () => {
    render(
      <WelcomeScreen 
        onStartGame={mockOnStartGame}
        onPlayWelcomeMusic={mockOnPlayWelcomeMusic}
      />
    );

    expect(screen.getByText('ðŸŽ‰ HAPPY BIRTHDAY! ðŸŽ‰')).toBeInTheDocument();
    expect(screen.getByText('To My Beautiful Wifey')).toBeInTheDocument();
    expect(screen.getByText('ðŸŽµ Falling Piano Tiles')).toBeInTheDocument();
  });

  it('should call onStartGame when start button is clicked', () => {
    render(
      <WelcomeScreen 
        onStartGame={mockOnStartGame}
        onPlayWelcomeMusic={mockOnPlayWelcomeMusic}
      />
    );

    const startButton = screen.getByText('ðŸŽµ Start Playing');
    fireEvent.click(startButton);

    expect(mockOnStartGame).toHaveBeenCalledTimes(1);
  });

  it('should call onPlayWelcomeMusic when music button is clicked', () => {
    render(
      <WelcomeScreen 
        onStartGame={mockOnStartGame}
        onPlayWelcomeMusic={mockOnPlayWelcomeMusic}
      />
    );

    const musicButton = screen.getByText('ðŸŽ¶ Play Welcome Music');
    fireEvent.click(musicButton);

    expect(mockOnPlayWelcomeMusic).toHaveBeenCalledTimes(1);
  });

  it('should render piano keys preview', () => {
    render(
      <WelcomeScreen 
        onStartGame={mockOnStartGame}
        onPlayWelcomeMusic={mockOnPlayWelcomeMusic}
      />
    );

    // Check for piano key notes
    expect(screen.getByText('C')).toBeInTheDocument();
    expect(screen.getByText('D')).toBeInTheDocument();
    expect(screen.getByText('E')).toBeInTheDocument();
    expect(screen.getByText('F')).toBeInTheDocument();
    expect(screen.getByText('G')).toBeInTheDocument();
  });

  it('should render love message', () => {
    render(
      <WelcomeScreen 
        onStartGame={mockOnStartGame}
        onPlayWelcomeMusic={mockOnPlayWelcomeMusic}
      />
    );

    expect(screen.getByText('Made with love just for you ðŸŽµ')).toBeInTheDocument();
  });
});
